import React, {Fragment} from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import styles from './Button.module.scss';

const Button = ({link, children, onClick, to, type}) => {
    return (
        <Fragment>
            {link ? 
                (
                    <Link 
                        className={styles.button} 
                        to={to}
                    >
                        {children}
                    </Link>
                ) :
                (
                    <button 
                        className={styles.button} 
                        type={type} 
                        onClick={onClick}
                    >
                        {children}
                    </button>
                )}
        </Fragment>
            
    );
    
};

Button.defaultProps = {
    type: 'button'
}

Button.propTypes = {
    to: PropTypes.string
};

export default Button;