import React, { useEffect, useState } from 'react';
import connect from '@vkontakte/vk-connect';
import { Math } from 'core-js';

import HeaderComponent from 'components/HeaderComponent';
import Button from 'components/Button/Button';
import SettingsList from 'components/settings/SettingsList';

import { returnStringParams } from 'utils/urlParamsHelper';

const Settings = ({appId, groupId, widgets}) =>  {
    const [widgetList, setWidget] = useState(widgets);
    const [params, setParams] = useState(null);

    useEffect(() => {
        setParams(returnStringParams());
    }, []);

    const handleEdit = (id) => console.log('Edit', id);

    const handleCopy = (id) => {
        const widget = widgetList.find(item => item.id === id);
        const objCopy = Object.assign({}, widget);
        const random = Math.floor(Math.random() * Math.floor(100));
        const string = String(random);
        objCopy.id = string;
        setWidget([...widgetList, objCopy]);
    };

    const handleDelete = (id) => {
        setWidget(widgetList.filter(item => item.id !== id));
    };

    const getToken = () => {
        connect.subscribe(({detail: {type, data}}) => {
            if(type === 'VKWebAppCommunityAccessTokenReceived') {
                sendToken(data.access_token);
            }

            if(type === 'VKWebAppGetCommunityAuthTokenResult') {
                sendToken(data.access_token);
            }

            if(type === 'VKWebAppCommunityAccessTokenFailed') {
                console.log('error', data)
            }
        });

        connect.send("VKWebAppGetCommunityAuthToken", {
            "app_id": Number(appId), "group_id": Number(groupId), "scope": "app_widget"
        });
    }

    const sendToken = token => {
        fetch(`https://apps.letscover.me/api/widgets/updateToken?accessToken=${token}&${params}`)
    };

    return (
        <div>
            <HeaderComponent title="Мои виджеты">
                <Button link to="/widget">Создать виджет</Button>
                <Button onClick={getToken}>Получить токен</Button>
            </HeaderComponent>
            <SettingsList 
                handleEdit={handleEdit} 
                handleCopy={handleCopy} 
                handleDelete={handleDelete} 
                widgetList={widgetList} 
            />
        </div>
    );
};

export default Settings;