import React, { useState } from 'react';
import Modal from 'components/Modal/Modal';
import Form from 'components/forms/TextForm.js';

import styles from './index.module.scss';

const TextWidget = ({handleSave, handleGetElement, element, position, fieldName, modal, setModal}) => {
  const [data, setData] = useState({});
  const [defaulValue, setDefaultValue] = useState({
    title: 'Важное сообщение',
    text: 'Нам нужно гордиться',
    descr: 'Дэвид Кэмерон о победе сторонников выхода страны\n из Евросоюза на референдуме',
    more: 'Читать все цитаты'
  });

  const handleSubmit = (e) => {
    e.preventDefault();
    
    const form = e.target;
    const obj = {}

    if(fieldName.name) {
        obj[form[`${fieldName.name}`].name] = form[`${fieldName.name}`].value;
    }

    if (fieldName.url) {
        obj[form[`${fieldName.url}`].name] = form[`${fieldName.url}`].value;
    }

    setData({...data, ...obj});
    handleChangeValue(form[`${fieldName.name}`].value);
  }

  const handleChangeValue = (value) => {
    element.innerText = value;
  }

  const save = () => {
    const obj = {
      title: data.title,
      title_url: data.title_url,
      more: data.more,
      more_url: data.more_url,
      rows: [
        {
          text: data.text,
          descr: data.descr
        }
      ]
    }
    handleSave(obj);
  }

  return (
    <div>
      <div className={styles.widget}>
          <div className={styles.widgetHeader}>
              <p
                onClick={(e) => handleGetElement(e)} 
                dataname="title" 
                dataurl="title_url" 
                className={styles.title} 
              >
                {defaulValue.title} 
              </p>
          </div>
          <div className={styles.widgetBody}>
              <p 
                onClick={(e) => handleGetElement(e)} 
                dataname="text" 
                className={styles.text}
              >
                {defaulValue.text}
              </p>
              <p 
                onClick={(e) => handleGetElement(e)} 
                dataname="descr" 
                className={styles.description}
              >
                {defaulValue.descr}
              </p>
          </div>
          <p
            dataname="more" 
            dataurl="more_url" 
            onClick={(e) => handleGetElement(e)} 
            className={styles.widgetFooter}
          >
            {defaulValue.more}
          </p>
          <Modal isOpen={modal} closeModal={setModal} position={position}>
              <Form 
                onSubmit={(e) => handleSubmit(e)}
                name={fieldName.name}
                url={fieldName.url}
              />
          </Modal>
      </div>
      <button onClick={save}>save</button>
    </div>
  );
};

export default TextWidget;