import React from 'react';

import SettingsButtonGroup from '../SettingsButtonGroup';

import { ReactComponent as Icon } from 'assets/image/list.svg';

import styles from './index.module.scss';

const SettingsListItem = ({ handleEdit, handleCopy, handleDelete, data }) => (
    <div className={styles.wrapper}>
        <div className={styles.infoWrapper}>
            <Icon className={styles.icon} />
            <div className={styles.title}>{data.title}</div>
            <div>{data.type}</div>
        </div>
        <SettingsButtonGroup handleEdit={handleEdit} handleDelete={handleDelete} handleCopy={handleCopy} id={data.id} />
    </div>
);

export default SettingsListItem;