import React from 'react';
import { NavLink } from 'react-router-dom';

import styles from './index.module.scss';

const Navigation = ({ routes, groupInfo }) => (
    <ul className={styles.menu}>
        <li className={styles.infoWrapper}>
            <img className={styles.logo} src={groupInfo.picture} alt='logo' />
            <p className={styles.name}>{groupInfo.name}</p>
        </li>
        {routes.map(route => 
            <li className={styles.menuItem} key={route.path}>
                <NavLink activeClassName={styles.activeClass} to={route.path} exact>{route.name}</NavLink>
            </li>
        )}
    </ul>
);

export default Navigation;