import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import connect from '@vkontakte/vk-connect';

import Landing from 'containers/Landing';
import Application from 'containers/Application';

import {getUrlParams} from './utils/urlParamsHelper';
import { urlParams } from './consts/urlParams';
import { returnStringParams } from 'utils/urlParamsHelper';

import './App.scss';

const App = () => {
	const [groupId, setGroupId] = useState(123);
	const [appId, setAppId] = useState(null);
	const [params, setParams] = useState(null);
	const [groupInfo, setGroupInfo] = useState({});

	const history = useHistory();

	useEffect(() => {
		setParams(returnStringParams());

		function checkGroup() {
			fetch(`https://apps.letscover.me/api/widgets/checkGroup?${params}`)
			.then(res => res.json())
			.then(json => setGroupInfo(json))
		}

		// setGroupId(getUrlParams(urlParams.vkGroupId));
		setAppId(getUrlParams(urlParams.vkAppId));

		function handleRedirect() {
			if(groupId) {
				history.push('/settings');
			}
		}

		if(groupId) {
			checkGroup();
		}

		handleRedirect();
	}, [history, groupId, params])

	async function connectToGroup() {
		await connect.sendPromise('VKWebAppAddToCommunity');
	}

	const renderApp = () => {
		if(groupId === null) {
			return <Landing handleConnect={connectToGroup} />;
		} else if (groupId) {
			return <Application groupId={groupId} appId={appId} groupInfo={groupInfo} />
		}
	};


	return <div>{renderApp()}</div>
}

export default App;

