import React from 'react';
import { Link } from 'react-router-dom';

import styles from './index.module.scss';

const WidgetCatalog = ({ widgets }) => <div className={styles.wrapper}>
    {widgets.map(widget => (
        <div className={styles.item} key={widget.type}>
            <img src={widget.image} alt="preview" className={styles.preview} />
            <div>
                <h3>{widget.title}</h3>
                <p>{widget.description}</p>
                <button><Link to={`/configuration/${widget.type}`}>Выбрать</Link></button>
            </div>
        </div>
    ))}
</div>;

export default WidgetCatalog;