import React from 'react';
import PropTypes from 'prop-types';
import Main from 'components/layout/Main';
import TilesLayout from 'components/layout/TilesLayout';

const Layout = ({ leftBar, rightBar, children }) => (
    <Main>
        {leftBar && (
            <TilesLayout flex={1}>{leftBar}</TilesLayout>
        )}
        {children && (
            <TilesLayout flex={3}>{children}</TilesLayout>
        )}
        {rightBar && (
            <TilesLayout flex={2}>{rightBar}</TilesLayout>
        )}
    </Main>
);

Layout.propTypes = {
    leftBar: PropTypes.node,
    rightBar: PropTypes.node,
    middleBar: PropTypes.func
};

export default Layout