import React, {Fragment} from 'react';
import styles from './Modal.module.scss';

const Modal = ({children, isOpen, closeModal, position}) => {
    const handleClickOnOverlay = () => {
        closeModal(false);
    };

    const customStyles = {
        top: position.top,
        left: 0
    };

    return !isOpen? null : (
        <Fragment>
            <div style={customStyles} className={styles.modalBody}>
                {children}
            </div>
            <div onClick={handleClickOnOverlay} className={styles.modalWrapper} />
        </Fragment>
    );
};

Modal.defaultProps = {
    isOpen: false
};

export default Modal;