import React, {useState} from 'react';

const RenderComponent = ({component: Component, ...props}) => {
    const [modal, setModal] = useState(false);
    const [position, setPosition] = useState({top: null, left: null});
    const [fieldName, setFieldName] = useState({name: null, url: null});
    const [element, setElement] = useState();

    const handleGetElement = (e) => {
        setModalPosition(e);
        handleDataAttribite(e);
        setElement(e.target);
    }
    
    const setModalPosition = (e) => {
    const element = e.target.getBoundingClientRect();
    setPosition({left: element.left, top: element.top});
    setModal(true);
    }
    
    const handleDataAttribite = (e) => {
    const nameAttr = e.target.getAttribute('dataname');
    const urlAttr = e.target.getAttribute('dataurl');
    setFieldName({name: nameAttr, url: urlAttr});
    }


    return <Component {...props} handleGetElement={handleGetElement} modal={modal} position={position} fieldName={fieldName} element={element} setModal={setModal} />;
};

export default RenderComponent;