import React from 'react';

import { ReactComponent as CopyIcon } from 'assets/image/copy.svg';
import { ReactComponent as RemoveIcon } from 'assets/image/rubbish-bin-delete-button.svg';
import { ReactComponent as EditIcon } from 'assets/image/pencil-edit-button.svg';

import styles from './index.module.scss';

const SettingsButtonGroup = ({ handleEdit, handleDelete, handleCopy, id }) => (
    <div>
        <CopyIcon onClick={() => handleCopy(id)} className={styles.icon} />
        <RemoveIcon onClick={() => handleDelete(id)} className={styles.icon} />
        <EditIcon onClick={() => handleEdit(id)} className={styles.icon} />
    </div>
);

export default SettingsButtonGroup;